@import url(https://fonts.googleapis.com/css?family=Lato);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  min-width: 100vw;
}

a.inline {
    color: lightblue;
}
a.inline:visited {
    color: mediumpurple;
}

.infopage {
    color: lightgrey;
    margin-left: 25px;
    margin-right: 25px;
}

.App {
    margin: 0;
}

/* the fixed position navbar mucks up alignment in weird ways... */
img.hack {
    height: 40px;
    visibility: hidden;
}

.zoomed-out {
    cursor: zoom-in;
}

.zoomed-in {
    position: fixed;
    top: 0;
    left: 0;
    cursor: zoom-out;
    z-index: 999 !important;
    max-height: 100vh !important;
    max-width: 100vw !important;
}

.dark-background {
    position: fixed;
    background-color: rgba(0,0,0,0.6);
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
}

.zoomable-img-wrapper {
    width: 100%;
    height: 100%;
    flex-basis: content;
    margin-left: 5px;
    margin-right: 5px;
}


.reflection-info-card {
    background-color: #33262b;
    border-radius: 10px;
    width: 250px;
    display: flex;
    flex-direction: row;
    height: 40%;
}

@media only screen and (max-width: 400px) {
    .reflection-info-card {
        width: 100%;
        height: auto;
    }
}

.reflection-card-column {
    display: flex;
    flex-direction: column;
    margin: 10px;
}

.reflection-icon {
    width: 60px;
    margin-top: 10px;
}

.CoR-title {
    color: white;
    font-size: 14px;
    font-weight: 500;
}

.CoR-description {
    font-size: 11px;
    color: white;
}

.level-input {
    margin-bottom: 10px;
    padding: 3px 5px;
    background-color: rgba(255,255,255,0.25);
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.CoR-level {
    display: inline-block;
    color: lightyellow;
    padding: 5px;
    font-size: 11px;
    font-weight: 500;
    letter-spacing: 0.5px;
    text-transform: uppercase;
}

.CoR-level-change {
    width: 20px;
    padding: 0;
    height: 22px;
}

.event-card-container {
    margin-right: 30px;
    background-color: #33262b;
    border-radius: 10px;
    padding: 10px;
    color: white;
}

.suit-link {
    font-size: 11px;
}

.event-title {
    font-size: 14px;
    font-weight: 500;
}

.event-subtitle {
    font-size: 11px;
    color: rgba(255,255,255,0.5);;
    margin-top: 5px;
}

.suit-detail-container {
    height: 325px;
    background-color: #eddae1;
    border-radius: 5px;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    left: 0;
    border: 20px solid #282c34;
    margin-top: 0px;
    padding: 20px;
    z-index: 10;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

@media only screen and (max-width: 468px) {
    .suit-detail-container {
        flex-direction: column;
        height: 520px;
        justify-content: flex-start;
    }
}

.suit-detail-left-column {
    width: 40%;
    display: flex;
    flex-direction: column;
    background-color: #e3bac2;
    margin-right: 40px;
    border-radius: 10px;
}

.suit-detail-img-container {
    padding: 10px;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
    height: 100%;
    min-height: 0;

}

.suit-detail-img {
    -webkit-user-select: none;
    flex-shrink: 1;
    max-height: 100%;
    max-width: 100%;
}

@media only screen and (max-width: 468px) {
    .suit-detail-left-column {
        width: 100%;
        min-width: 0;
        min-height: 0;
    }

    .suit-detail-img-container {
        padding-top: 10px;
        min-width: 0;
        min-height: 0;
        width: 100%;
        height: 100%;
        justify-content: space-around;
    }
}

div {
    border: none;
    background-color: transparent;
    resize: none;
    outline: none;
}

.suit-title {
    font-weight: 500;
    font-size: 32px;
    color: black;
    margin-bottom: 3px;
    font-style: normal;
    margin-top: 10px;
    font-family: Candara, sans-serif;
}

.suit-title-block {
    margin-bottom: 8px;
    margin-right: 20px;
    width: 60%;
    font-size: 14px;
    text-align: left;
}

@media only screen and (max-width: 468px) {
    .suit-title-block {
        width: 100%;
        font-size: 12px;
    }
    .suit-title {
        font-size: 24px;
    }
}

.suit-detail-type-button-container {
    width: 130px;
    background-color: #33262b;
    border-radius: 10px;
    margin-right: 20px;
    margin-top: 0;
    padding: 20px;
    flex-shrink: 0;
}


@media only screen and (max-width: 468px) {
    .suit-detail-type-button-container {
        width: auto;
        margin-bottom: 10px;
        margin-right: 0;
        padding-bottom: 10px;
    }
}

.suit-detail-type-button {
    width: 75px;
    margin-left: 0;
    margin-right: 8px;
    margin-bottom: 8px;
    border-radius: 50px;
    border-style: none;
    font-weight: bold;
    padding: 5px;
    font-size: 12px;
}

@media only screen and (max-width: 468px) {
    .suit-detail-type-button {
        width: auto;
        margin-right: 5px;
    }
}


.selected {
    background-color: #d1668f;
    color: white;
}

.suit-detail-header-container {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
}

.suit-image-buttons-container {
    margin-top: 10px;
    margin-bottom: 15px;
}

.suit-subtitle-container {
    display: inline-flex;
    font-family: sans-serif;
    opacity: 0.5;
    flex-direction: row;
    align-items: center;
    margin-right: 5px;
    margin-bottom: 6px;
}

.suit-detail-icon-container {
    margin-left: 8px;
}

@media only screen and (max-width: 468px) {
    .suit-detail-icon-container {
        flex-direction: row;
    }
}

.favourite-icon-container {
    width: 20px;
    height: 20px;
    margin-top: 8px;
}

.heart-icon {
    width: 100%;
    height: 100%;
    width: 20px;
    height: 20px;
}

.favourite-icon-container.detail {
    width: 100%;
    height: 35px;
    background-color: rgba(0,0,0,0.4);
    border-radius: 0px 0px 10px 10px;
    box-sizing: border-box;
    padding: 5px;
    padding-right: 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.heart-icon.unfavourited {
    filter: invert(100%);
    opacity: 0.5;
}

.suit-detail-close-button {
    position: absolute;
    right: -10px;
    top: -10px;
    width: 36px;
    height: 36px;
    border-radius: 18px;

}

.suit-icon.detail {
    padding: 5px;
}

.suit-attribute-icons {
    display: flex;
    align-items: center;
}

.suit-attribute-label {
    font-size: 11px;
    font-style: italic;
    color: lightgray;
    padding-right: 10px;
}


.suit-likes-container {
    display: flex;
}

.suit-attribute-label.favourites {
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
}

.suit-detail-infocard-container {
    display: flex;
    flex-direction: row;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

.suit-cards-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 8px;
    position: fixed;
    height: 100vh;
}

.suit-cards-container.loaded {
    overflow-y: scroll;
}

.suit-card {
    width: 150px;
    height: 220px;
    margin: 8px;
    border: 1px solid #282c34;
}

.suit-card.active {
    filter: drop-shadow(0px 0px 8px lightpink);
    border: 1px solid lightpink;
}

.suit-img {
    display: block;
    width: 150px;
    height: 220px;
    object-fit: cover;
}


.suit-name {
    position: absolute;
    padding-left: 10px;
    margin-top: -40px;
    font-size: 12px;
    color: white;
    max-width: 115px;
    background-color: rgba(0,0,0,0.6);
    padding-bottom: 5px;
    padding-top: 5px;
    padding-right: 10px;
    border-radius: 0px 5px 5px 0px;
}

@media only screen and (max-width: 400px) {
    .suit-card {
        width: 125px;
        height: 180px;
    }
    .suit-img {
        width: 125px;
        height: 180px;
    }
    .suit-name {
        max-width: 105px;
    }
}

.suit-icon-container {
    position: absolute;
    display: flex;
    margin: 5px;
    flex-direction: column;
}

.suit-icon {
    margin: 3px;
    width: 20px;
    height: 20px;
}

.suit-icon.shadowed {
    filter: drop-shadow(0px 0px 5px white);
}

.narrow {
    margin-left: 215px;
}

.wide {
    margin-left: 55px;
}

.fb-login-button {
    background: center/cover url(data:image/png;base64,) no-repeat;
    width: 150px;
    height: 35px;
}


.suit-filter-container {
    height: 100%;
    min-width: 12px;
    position: fixed;
    margin: 18px 12px;
    background-color: rgba(255,255,255,0.4);
    padding: 15px;
    display: flex;
    flex-direction: column;
}

input {
    padding-left: 5px !important;
}

.filter-title {
    width: 170px;
    margin-bottom: 15px;
    margin-top: 5px;
    font-size: 24px;
}

.filter-label {
    font-weight: normal;
    font-size: 13px;
}

.toggle-filter-btn {
    position: absolute;
    right: 8px;
    top: 12px;
}

.toggle-filter-icon {
    width: 14px;
    background-color: rgba(255,255,255,0.55);
    padding: 5px;
    border-radius: 10px;
}

.toggle-filter-label {
    font-size: 9px;
    text-align: center;
    width: 25px;
    font-style: italic;
    font-weight: 500;
}

.search-bar-wrapper {
    margin-bottom: 10px;
}

.first {
    z-index: 10 !important;
}

.hidden {
    display: none;
}

.filter-box-container {
    background-color: rgba(255,255,255,0.6);
    width: 100%;
    margin: 8px 0px;
    font-size: 14px;
    font-weight: 700;
    color: #383838;
}

.filter-box-title {
    font-weight: 600;
    font-size: 14px;
    background-color: rgba(0,0,0,0.6);
    color: white;
    letter-spacing: 0.7px;
    padding: 8px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.filter-icon {
    width: 15px;
    height: 15px;
    margin: 0px 5px;
}

.filter-option-container {
    padding: 5px;
}

.child-option-container {
    display: flex;
    flex-direction: column;
    margin-left: 25px;
}

.filter-option {
    display: flex;
    flex-direction: row;
    padding-left: 5px;
    align-items: center;
}

.suit-img-tiles-container {
    display: flex;
    flex-flow: row wrap;
}

.suit-img-tile {
    display: flex;
    background-color: gray;
    border-radius: 10px;
    flex-direction: column;
    margin: 10px;
}

.suit-tile-img-wrapper {
    height: 30vh;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    padding: 5px;
}

.tile-img {
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
    flex-shrink: 1;
    min-width: 0;
}

.tile-img.suit {
    border-radius: 0px 0px 0px 10px;
}

.tile-img.reflection {
    margin-left: 5px;
}

.suit-tile-title {
    font-size: 16px;
    font-weight: bold;
    text-transform: capitalize;
    padding: 3px 10px 5px;
    margin: 5px;
}

.suit-detail-info-container {
    background-color: #9c676b;
    padding: 20px;
    display: flex;
    justify-content: space-between;
}

.suit-detail-icon-container.horizontal {
    flex-direction: row;
    display: flex;
    align-items: flex-end;
}

.suit-source.full {
    font-size: 14px;
    margin-right: 8px;
}

.suit-title.full {
    font-size: 38px;
    font-family: Candara, sans-serif;
    font-weight: normal;
}

.suit-title-container {
    margin: 8px;
}

@media only screen and (max-width: 400px) {
    .suit-detail-header-container {
        flex-direction: column;
        align-items: flex-start;
    }
    .suit-detail-icon-container.horizontal {
        margin-left: 0;
    }
    .suit-source.full {
        font-size: 11px;
    }
    .suit-title.full {
        font-size: 26px;
    }
}

/* modified from https://codepen.io/madshaakansson/pen/xwmzK */

nav * {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  top: 0;
  left: 0;
  font-family: 'Lato', sans-serif;

}

nav {
  top: 0;
  left: 0;
  width: 100vw;
  background: #fff;
  box-shadow: 0 3px 10px -2px rgba(0,0,0,.1);
  border: 1px solid rgba(0,0,0,.1);
  overflow-y: hidden;
  position: fixed;
  color: #222;
  font-size: .9em;
  letter-spacing: 1px;
  text-decoration: none;
  text-transform: uppercase;
}

@media only screen and (max-width: 770px) {
  nav {
    height: 40px;
    min-height: 40px;
  }
}

nav > b {
  display: inline-block;
  padding: 10px 20px;
  float: left;
}

nav > ul {
list-style: none;
position: relative;
float: left;
margin-left: 40px;
margin-right: 16px;
}

nav > ul > li{
  float: left;
  transition: all .2s ease-in-out;
}

nav > ul li:hover{
  background: rgba(0,0,0,.15);
}

nav > ul li:hover > ul{
  display: block;
}

nav > ul li {
  float: left;
  transition: all .2s ease-in-out;
}

nav > ul ul li::marker {
  color: transparent;
}

nav ul li a {
  display: block;
  padding: 12px 20px;
  color: #222;
  font-size: .9em;
  letter-spacing: 1px;
  text-decoration: none;
  text-transform: uppercase;
}

nav > ul ul {
  display: none;
  background: #fff;
  position: absolute;
  z-index: 999;
  top: 100%;
  box-shadow: -3px 3px 10px -2px rgba(0,0,0,.1);
  border: 1px solid rgba(0,0,0,.1);
}

nav > ul ul li a {
  padding: 15px 30px;
  border-bottom: 1px solid rgba(0,0,0,.05);
}

.mobile-hamburger-menu {
    display: none;
}

@media only screen and (max-width: 770px) {
    .mobile-hamburger-menu {
        width: 20px;
        height: 20px;
        margin-right: 10px;
        display: inline-block;
    }

    nav.open {
        height: auto;
        z-index: 9999;
    }
}

